@font-face {
  font-family: "Azonix";
  src: local("Azonix"), url("./azonix/Azonix.otf") format("otf");
}
body {
  margin: 0;
  padding: 0;
  font: 1rem "Google Sans Regular";
  min-height: 100vh;
  background: linear-gradient(120deg, coral, purple);
  background-size: cover;
  background-repeat: no-repeat;
}

h1 {
  color: rebeccapurple;
  font-family: "Azonix", sans-serif;
  letter-spacing: 1rem;
}
